import { CButton, CCard, CCardBody, CCardFooter, CCardHeader } from "@coreui/react";
import React, { useEffect, useState } from "react";
import LoadingButton from "../../components/LoadingButton";
import LoadingState from "../../components/LoadingState";
import { showCyberTipline, updateCyberTipline } from "../../api";
import qs from "qs";
import FormText from "./components/form/tabs/FormText";

const CyberTiplineEdit = (props) => {
  const queryString = qs.parse(props.location.search, { ignoreQueryPrefix: true });
  const id = queryString.id;
  const [loadingButton, setLoadingButton] = useState(false);
  const [defaultData, setDefaultData] = useState({});
  const [loadingApi, setLoadingApi] = useState(true);
  const [data, setData] = useState({});

  const onSubmit = async () => {
    setLoadingButton(true);
    let x = { ...data };
    let ip = JSON.stringify(x.suspectIpAddress.map((v) => v.value));
    let emails = x.suspectEmails.map((s) => ({ id: s.value, email: s.label, cybertipline: { id: id } }));
    x.suspectIpAddress = ip;
    x.suspectEmails = emails;
    x.id = id;
    try {
      await updateCyberTipline(x);
      window.history.back();
    } catch (err) {
      console.log(err);
    } finally {
      setLoadingButton(false);
    }
  };

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const { data } = await showCyberTipline(id);
      const ips = JSON.parse(data.suspectIpAddress).map((ip) => ({ value: ip, label: ip }));
      const emails = data.suspectEmails.map((em) => ({ value: em.id, label: em.email }));
      const x = {
        ctNumber: data.ctNumber,
        priority: data.priority,
        reportingEsp: data.reportingEsp,
        suspectEmails: emails,
        suspectScreenName: data.suspectScreenName,
        suspectIpAddress: ips,
        dateMadeAvailable: data.dateMadeAvailable.split("T")[0],
        notes: data.notes,
        caseInformation: data.caseInformation,
        suspectPhone: data.suspectPhone,
        suspectDob: data.suspectDob.split("T")[0],
        incidentDate: data.incidentDate.split("T")[0],
      };
      console.log(x);
      setDefaultData(x);
    } catch (err) {}
    setLoadingApi(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  if (loadingApi) return <LoadingState />;
  return (
    <CCard>
      <CCardHeader>
        <h5 className="d-inline">Edit Cyber Tipline</h5>
      </CCardHeader>
      <CCardBody>
        <FormText defaultData={defaultData} getData={(e) => setData(e)} />
      </CCardBody>
      <CCardFooter>
        {loadingButton ? (
          <LoadingButton />
        ) : (
          <CButton color="primary" onClick={onSubmit}>
            Simpan
          </CButton>
        )}{" "}
        <CButton color="secondary" onClick={() => window.history.back()}>
          Kembali
        </CButton>
      </CCardFooter>
    </CCard>
  );
};

export default CyberTiplineEdit;
